@import "../variables";
@import "../components/resp.scss";
.banner {
    display: flex;

    .banner-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        margin: 14px;
    }
    .card {
        position: relative;
        width: 100%;
        min-height: 203px;
        height: auto;
        overflow: hidden;
        padding: 2rem 2rem 1rem 2rem;
        background: #090506;
        background: linear-gradient(to bottom, #090506, #070709);
        .text {
            font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-48) /
                var(--unnamed-line-spacing-60) var(--unnamed-font-family-source-serif-pro);
            letter-spacing: var(--unnamed-character-spacing-0);
            color: var(--white);
            text-align: center;
            opacity: 1;
        }
        .line {
            border-top: 8px solid var(--primary-d1);
            width: 20%;
            margin: auto;
        }
    }

    @media screen and (max-width: $Tablet) and (min-width: ($Mobile )) {
        .container {
            max-width: $Tablet;
            padding: 0px 0px 0px 0px;
        }
        .row {
            margin: 0px 0px;
        }
        .phn-container {
            padding: 0rem 1rem;
        }
        .banner-img {
            margin: 0px;
        }
        .card {
            padding: 0rem 0rem 0rem 0rem;
            min-height: 159px;
            margin-top: -30px;
            .text {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36) /
                    42px var(--unnamed-font-family-source-serif-pro);
                padding: 2rem 0rem 0rem 0rem;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
    @media screen and (max-width: $Desktop) and (min-width: ($Tablet + 1)) {
        .card {
            padding: 0rem 2rem 0rem 2rem;
            .text {
                padding: 2rem 0rem 0rem 0rem;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
    @media screen and (max-width: $Desktop-md) and (min-width: ($Desktop + 1)) {
        .card {
            padding: 2rem 2rem 2rem 2rem;
            .text {
                padding: 2rem 0rem 0rem 0rem;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
    @media screen and (max-width: $Desktop-lg) and (min-width: ($Desktop-md + 1)) {
        .card {
            padding: 3rem 2rem 3.5rem 2rem;

            .text {
                padding: 3rem 2rem 2rem 2rem;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
    @media screen and (max-width: ($Desktop-lg + 96)) and (min-width: ($Desktop-md + 1)) {
        .card {
            padding: 3rem 2rem 3.5rem 2rem !important;

            .text {
                padding: 3rem 2rem 2rem 2rem ;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
    @media screen and (max-width: $Desktop-xl) and (min-width: ($Desktop-lg + 1)) {
        .card {
            padding: 7rem 2rem 6.5rem 2rem;
            .text {
                padding: 3rem 6rem 3rem 6rem;
                margin-block-start: 0em;
                margin-block-end: 0em;
            }
        }
    }
}
